.slider {
  height: 40px;
  border: 2px solid black;
  border-radius: 20px;
}

.thumb {
  background-color: black;
  cursor: pointer;
  width: 40px;
  height: 38px;
  border-radius: 20px;
}

.slider.break {
  border: 2px solid black;
}
.slider.green .break {
  background-color: black;
}
